import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {httpLoading} from '@/store/actions';
import './style.css';
import {GETAUDIENCEMANAGER, GETSEARCHDETAIL} from '@/api/index';
import {get, post} from '@/utils/request';
import {Table, Card, Button, Space, Tooltip} from 'antd';
import TaskItem from '@/pages/audienceManager/component/TaskItem';
import TaskCol from '@/pages/audienceManager/component/TaskCol';
import {message} from 'antd';
import {RedoOutlined, SaveOutlined, FileSearchOutlined} from '@ant-design/icons';
const STATUS_CODE = {
  STATUS_GENERATED: 'Generated',
  STATUS_TEST: 'Test',
  STATUS_WINNER: 'Winner',
  STATUS_ARCHIVE: 'Archive',
};
const STATUS_GENERATED = 'STATUS_GENERATED';
const STATUS_TEST = 'STATUS_TEST';
const STATUS_WINNER = 'STATUS_WINNER';
const STATUS_ARCHIVE ='STATUS_ARCHIVE';

const AudienceManger = ({userInfo, httpLoading, setHttpLoading}) => {
  const [tasks, setTasks]=useState(null);
  const [activeId, setActiveId] =useState(null);
  const [details, setDetails]=useState(null);
  const getAudienceManager=()=>{
    post(GETAUDIENCEMANAGER, {pageNum: 1, pageSize: 10000}, {
      // eslint-disable-next-line no-tabs
      'Content-Type': 'application/x-www-form-urlencoded',
      'token': userInfo.token,
    }).then((res)=>{
      console.log(res);
      setTasks(res.data.map((item)=>{
        return {
          ...item,
          status: item.status===1?STATUS_GENERATED:
                  item.status===2?STATUS_TEST:
                  item.status===3?STATUS_WINNER:STATUS_ARCHIVE,
          type: item.type===1?'Generated':
            item.type===2?'Test':
              item.type===3?'Winner':'Archive',
        };
      }));
    }).catch((error) => {
      message.error({
        content: error.toString(), key: 'netError', duration: 2,
      });
    });
  };
  const getDetails=(id)=>{
    get(GETSEARCHDETAIL+id, userInfo.token).then((res)=>{
      setDetails(res.data);
    }).catch((error) => {
      message.error({
        content: error.toString(), key: 'netError', duration: 2,
      });
    });
  };

  useEffect(()=>{
    getAudienceManager();
  }, []);

  const onDragStart = (id, t) => {
    setActiveId(id);
    console.log(id, t);
  };
  const dragTo = (status) => {
    console.log(1);
    const task = tasks[activeId];
    if (task.status !== status) {
      task.status = status;
      setTasks(tasks);
    }
    cancelSelect();
  };
  const cancelSelect = () => {
    setActiveId(null);
    console.log(3);
  };

  return (
    <div>
      <div className="task-wrapper">
        {
          Object.keys(STATUS_CODE).map((status) =>
            <TaskCol
              status={status}
              key={status}
              dragTo={()=>dragTo(status)}
              canDragIn={activeId !== null && tasks[activeId].status !== status}>
              { tasks&&tasks.filter((t) => t.status === status).map((t, index) =>
                <TaskItem
                  key={t.id}
                  active={index === activeId}
                  info={t}
                  onClicks={()=>getDetails(t.id)}
                  onDragStart={()=>onDragStart(index, t)}
                  onDragEnd={()=>cancelSelect(index)}
                />)
              }
            </TaskCol>,
          )
        }
      </div>
      {details&&(
        <div>
          <h2>Audience Tracker</h2>
          <p>Fill ad testing results for tracking and your custom audience set&apos;s optimization.</p>
          <p>Click &ldquo;Extend&ldquo; to expand custom keywords from 50 to 300.</p>
          <Card>
            <div className="text-right"> <Button>Save All</Button></div>
            <Table dataSource={details} pagination={false}>
              <Table.Column dataIndex="groupId" key="groupId" title="Group ID"/>
              <Table.Column dataIndex="spend" key="spend" title="Spend"/>
              <Table.Column dataIndex="impression" key="impression" title="Impression"/>
              <Table.Column dataIndex="clickCount" key="clickCount" title="Click"/>
              <Table.Column dataIndex="install" key="install" title="Install"/>
              <Table.Column dataIndex="purchase" key="purchase" title="Purchase"/>
              <Table.Column dataIndex="roas" key="roas" title="ROAS"/>
              <Table.Column dataIndex="cpi" key="cpi" title="CPI(Spend/Install)"/>
              <Table.Column key="action" title="Action" render={()=>(
                <Space>
                  <Tooltip title="Running">
                    <a type="link">
                      <RedoOutlined />
                    </a>
                  </Tooltip>
                  <Tooltip title="Save">
                    <a type="link">
                      <SaveOutlined />
                    </a>
                  </Tooltip>
                  <Tooltip title="View Details">
                    <a type="link">
                      <FileSearchOutlined />
                    </a>
                  </Tooltip>
                  <Tooltip title="Restart the job">
                    <a type="link">
                      <SaveOutlined />
                    </a>
                  </Tooltip>
                  <Tooltip title="Restart the job">
                    <a type="link">
                      <SaveOutlined />
                    </a>
                  </Tooltip>
                </Space>
              )}/>
            </Table>
          </Card>
        </div>
      )}
    </div>

  );
};

const mapStateToProps = (state) => {
  return {
    httpLoading: state.toggleHttpLoading.loading,
    userInfo: state.getUserInfo.info,
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    setHttpLoading: (f) => dispatch(httpLoading(f)),
  };
};

AudienceManger.propTypes = {
  userInfo: PropTypes.object.isRequired,
  httpLoading: PropTypes.bool.isRequired,
  setHttpLoading: PropTypes.func.isRequired,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(AudienceManger);
